.home-container {
  position: relative;
  min-height: 100vh;
  overflow: hidden;
}

.falling-food {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}

.falling-food span {
  position: absolute;
  font-size: 2rem;
  animation: fall linear infinite;
  animation-duration: 10s;
  animation-delay: calc(var(--delay) * -1s);
  left: calc(var(--left) * 1%);
}

.falling-food span:nth-child(1) {
  --delay: 2.3;
  --left: 10;
}
.falling-food span:nth-child(2) {
  --delay: 7.8;
  --left: 20;
}
